
export default {
	onLaunch: function() {
		const token = uni.getStorageSync('token');
		const userInfo = uni.getStorageSync('user_info');
		if(!token || !userInfo) {
			uni.navigateTo({
				url: './pages/login/login'
			});
		}
	},
	onShow: function() {
		
	},
	onHide: function() {
		console.log('App Hide');
	}
};
